import { HOC } from "../../../common/hoc/types";
import { useStudentSelector } from "../../store";
import { Typography } from "@mui/material";
import { Avatar } from "../../../common/component/Avatar";
import { avatarHeight, photoHeight } from "../../selectors/novigation";
import { MetricItemContainer, MetricsContainer, ProfileWrapper } from "../../../common/component/MetricStyled";
import { selectStudentAbonentsAmount, selectStudentAvatar, selectStudentSubjectsAmount } from "../../selectors/domain";

export const StudentProfile: HOC = () => {

    const subjectsNumber = useStudentSelector(selectStudentSubjectsAmount);
    const abonementsNumber = useStudentSelector(selectStudentAbonentsAmount);
    const photoUrl = useStudentSelector(selectStudentAvatar );

    // const dispatch = useDispatch();

    return (
        <ProfileWrapper minHeight={photoHeight}>
            <MetricsContainer height={photoHeight}>
                <MetricItemContainer>
                    <Avatar image={photoUrl} size={avatarHeight} onClick={() => undefined} />
                </MetricItemContainer>
                <MetricItemContainer>
                    <Typography textAlign="center">{subjectsNumber}</Typography>
                    <Typography textAlign="center">Subjects</Typography>
                </MetricItemContainer>
                <MetricItemContainer>
                    <Typography textAlign="center">{abonementsNumber}</Typography>
                    <Typography textAlign="center">Abonements</Typography>
                </MetricItemContainer>
            </MetricsContainer>
        </ProfileWrapper>
    );
}
    