import { Alert, Box } from "@mui/material";
import { HOC } from "../../../common/hoc/types";
import { ISubjectView } from "../../../common/types/teacherDomainView";
import { useStudentSelector } from "../../store";
import { Card } from "../../../common/component/Card";
import { IconButton } from "../../../common/component/IconButton";
import { ButtonType } from "../../../common/component/IconNavButton";
import { TitleStyledTypography } from "../../../common/component/LessonHeader";
import { getRuleFormatted } from "../../../common/utilities/getRuleFormatted";
import { selectStudentProfileSubjects } from "../../selectors/domain";

interface IStudentSubjectCardProps extends ISubjectView {
  onClickCommand: () => void;
  infoCommand: () => void;
};


export const StudentSubjectCard: React.FC<IStudentSubjectCardProps> = ({name, rules, onClickCommand, infoCommand}) => {

  const ruleFormatted = getRuleFormatted(rules);

  return (
    <Card
      children={[
        <TitleStyledTypography key='subjectName'>{name}</TitleStyledTypography>,
        <IconButton
          key='subjectInfo'
          type={ButtonType.info}
          onClick={infoCommand}
        />,
        <TitleStyledTypography
          key='subjectRulesInfo'
        >
          {ruleFormatted}
        </TitleStyledTypography>,
      ]}
      onClick={onClickCommand}
    />
  )
}

export const StudentSubjectList: HOC = () => {

  // const dispatch = useDispatch();
  const subjects = useStudentSelector(selectStudentProfileSubjects);

  return (
    <Box height={'100%'} overflow={'auto'}>
      {!!subjects.length ?
        subjects.map((subject) => {
          return (
            <StudentSubjectCard
              key={subject.id}
              onClickCommand={() => console.log(`click ${subject.id}`)}
              infoCommand={() => console.log(`info ${subject.id}`)}
              {...subject}
            />
          )
        }): 
        <Alert severity="info">this list is empty</Alert>
      }
    </Box>
  );
}
