import { HOC } from "../../../common/hoc/types";
import { useStudentSelector } from "../../store";
import { selectAbonementPageNavigation } from "../../selectors/novigation";
import { StudentNavigationContainer } from "../StudentProfileNavigation";
import { ButtonType, IconNavButton } from "../../../common/component/IconNavButton";
import { AbonementPageNavigation } from "../../store/modal";

export const AbonementPageNavigationHoc: HOC = () => {

  const abonementPageView = useStudentSelector(selectAbonementPageNavigation);

  const isSubjectsActive = abonementPageView === AbonementPageNavigation.subjects;
  const isAbonementsActive = abonementPageView === AbonementPageNavigation.lessons;

  return (
    <StudentNavigationContainer>
      <IconNavButton
        type={ButtonType.abonements}
        onClick={() => console.log('lessons')}
        title="lessons"
        isActive={isAbonementsActive}
      />
      <IconNavButton
        type={ButtonType.subjects}
        onClick={() => console.log('subjects')}
        title="subjects"
        isActive={isSubjectsActive}
      />
    </StudentNavigationContainer>
  ); 
}
