import { Box, Typography } from "@mui/material";
import { HOC } from "../../../common/hoc/types";
import { useStudentSelector } from "../../store";
import { NotificationButton } from "../../../teacher/hoc/NotificationButton";
import { selectIsNameEditing , selectEditableStudentName  } from "../../selectors/editable";
import { ClickOutsideHandler } from "../../../common/component/ClickOutsideHandler";
import { Header } from "../../../common/component/Header";
import { TextField, TextFieldType } from "../../../common/component/TextField";
import { selectCurrentStudentName } from "../../selectors/domain";

export const StudentProfilePageHeader: HOC = () => {

  // const dispatch = useDispatch();
  const currentStudentName = useStudentSelector(selectCurrentStudentName);
  const editableStudentName = useStudentSelector(selectEditableStudentName );

  const isNameEditing = useStudentSelector(selectIsNameEditing );

  return (
    <ClickOutsideHandler onClickOutside={() => console.log('clickOutside')}>
      <Header>
        {isNameEditing ? 
            <Box width="100%" overflow="auto" height="100%">
              <TextField
                type={TextFieldType.header} 
                text={editableStudentName}
                onChange={(text) =>  console.log('onChange', text)}
              />
            </Box>
          :
          <Typography
            noWrap
            textOverflow='ellipsis'
            overflow='hidden'
            maxWidth='17rem'
            onClick={() => console.log('onClick')}
          >
            {currentStudentName ? currentStudentName : 'click to add your name'}
          </Typography>
        }
        <NotificationButton />
      </Header>
    </ClickOutsideHandler> 
  );
}
