import { useEffect, useRef } from "react";
import { HOC } from "../../../common/hoc/types";
import { selectTeacherForStudentDescriptionHeight } from "../../selectors/dom";
import { useStudentSelector } from "../../store";
import React from "react";
import { avatarHeight, descriptionDefaultHeight, photoHeight } from "../../../teacher/selectors/dom";
import { Typography } from "@mui/material";
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { Avatar } from "../../../common/component/Avatar";
import { TextButton } from "../../../common/component/TextButton";
import { selectIsShowMore } from "../../selectors/novigation";
import { DescriptionContainer, MetricItemContainer, MetricsContainer, ProfileWrapper } from "../../../common/component/MetricStyled";
import { selectExternalTeacherAvatar, selectExternalTeacherDescription, selectTeacherAbonementsAmount, selectTeacherSubjetsAmount } from "../../selectors/external";

export const TeacherProfileForStudent: HOC = () => {

const isShowMore = useStudentSelector(selectIsShowMore);
const descriptionHeight = useStudentSelector(selectTeacherForStudentDescriptionHeight);
const subjectsAmount = useStudentSelector(selectTeacherSubjetsAmount);
const offersAmount = useStudentSelector(selectTeacherAbonementsAmount);
const teacherAvatar = useStudentSelector(selectExternalTeacherAvatar);
const profileDescription = useStudentSelector(selectExternalTeacherDescription);

// const dispatch = useDispatch();

const descriptionContainerRef = useRef<HTMLDivElement | null>(null);
const [hasShowMore, setHasShowMore] = React.useState(false);

useEffect(() => {
  if (descriptionContainerRef.current) {
    const container = descriptionContainerRef.current;
    const contentHeight = container.scrollHeight;
  
    setHasShowMore( contentHeight > descriptionDefaultHeight);
    container.scrollTop = 0;
  }
}, [profileDescription, isShowMore]);

return (
  <ProfileWrapper minHeight={photoHeight + descriptionHeight}>
    <MetricsContainer height={photoHeight}>
      <MetricItemContainer>
        <Avatar image={teacherAvatar} size={avatarHeight} onClick={() => undefined} />
      </MetricItemContainer>
      <MetricItemContainer>
        <Typography textAlign="center">{subjectsAmount}</Typography>
        <Typography textAlign="center">Subjects</Typography>
      </MetricItemContainer>
      <MetricItemContainer>
        <Typography textAlign="center">{offersAmount}</Typography>
        <Typography textAlign="center">Abonements</Typography>
      </MetricItemContainer>
    </MetricsContainer>
    <DescriptionContainer
      ref={descriptionContainerRef}
      height={descriptionHeight}
      maxHeight={ descriptionHeight }
      overflow={ isShowMore ? "auto" : "hidden" }
      onClick={ () => console.log('click') }
    >
      <Typography
        style={{ wordWrap: 'break-word' }}
        gutterBottom
        onClick={()=>console.log('click')}
      >
        {profileDescription ? profileDescription : ''}
      </Typography>
    </DescriptionContainer>
      {(hasShowMore && !isShowMore) && (
        <TextButton
          title='See more'
          onClick={() => console.log('See more')}
        >
          { isShowMore ? <KeyboardArrowUpIcon  /> : <KeyboardArrowDownIcon /> }
        </TextButton>
      )}
    </ProfileWrapper>
  );
}
