import { Alert, Box } from "@mui/material";
import { HOC } from "../../../common/hoc/types";
import { dummySubjectViews } from "../../../common/component/dummies";
import { StudentSubjectCard } from "../StudentSubjectList";
import { useStudentSelector } from "../../store";
import { selectTeacherSubjectsForStudent } from "../../selectors/external";

export const TeacherSubjectListForStudent: HOC = () => {

  // const dispatch = useDispatch();
  const subjects = useStudentSelector(selectTeacherSubjectsForStudent);

  return (
    <Box height={'100%'} overflow={'auto'}>
      {!!subjects.length ?
        subjects.map((subject) => {
          return (
            <StudentSubjectCard
              key={subject.id}
              onClickCommand={() => console.log(`click ${subject.id}`)}
              infoCommand={() => console.log(`info ${subject.id}`)}
              {...subject}
            />
          )
        }): 
        <Alert severity="info">this list is empty</Alert>
      }
    </Box>
  );
}
