import { HOC } from "../../../common/hoc/types";
import { selectStudentProfileNavigation } from "../../selectors/novigation";
import { useStudentSelector } from "../../store";
import { StudentProfileNavigation } from "../../store/modal";
import { StudentAbonementList } from "../StudentAbonementList";
import { StudentSubjectList } from "../StudentSubjectList";

export const StudentProfileWindow: HOC = () => {

  const studentProfilePageView = useStudentSelector(selectStudentProfileNavigation);
    
  switch (studentProfilePageView) {
    case StudentProfileNavigation.subjects: {
      return (
        <StudentSubjectList />
      )
    }
    case StudentProfileNavigation.abonements: {
      return (
        <StudentAbonementList />
      )
    }
    default: return <></>;
  }
}
