import { HOC } from "../../../common/hoc/types";
import { selectAbonementPageNavigation } from "../../selectors/novigation";
import { useStudentSelector } from "../../store";
import { AbonementPageNavigation } from "../../store/modal";
import { AbonementSubjectList } from "../AbonementSubjectList";
import { LessonsCalendar } from "../LessonsCalendar";
import { LessonsList } from "../LessonsList";

export const AbonementWindow: HOC = () => {

  const abonementPageView = useStudentSelector(selectAbonementPageNavigation)

  switch (abonementPageView) {
    case AbonementPageNavigation.subjects: {
      return <AbonementSubjectList />
    };
    case AbonementPageNavigation.lessons: {
      return (
        <>
          <LessonsCalendar />
          <LessonsList />
        </>
      );
    };
    default: return <></>
  }
}
