import { IAbonementConsumableView, IStudentView } from "../../common/types/studentDomainView";
import { ISubjectView } from "../../common/types/teacherDomainView";
import { StudentSelector } from "./types";

export const selectStudentDomain: StudentSelector<IStudentView> = (state) => state.studentDomain;
export const selectStudentAvatar: StudentSelector<string> = (state) => {

  const avatarUrl = selectStudentDomain(state).avatar;

  return avatarUrl;
};

export const selectCurrentStudentName: StudentSelector<string> = (state) => selectStudentDomain(state).name;

export const selectStudentProfileAbonements: StudentSelector<IAbonementConsumableView[]> = (state) => selectStudentDomain(state).abonementConsumables;

export const selectStudentAbonentsAmount: StudentSelector<number> = (state) => selectStudentProfileAbonements(state).length;


export const selectStudentProfileSubjects: StudentSelector<ISubjectView[]> = (state) => {
  const studentConsumable = selectStudentProfileAbonements(state);
  const allSsubjects = studentConsumable.flatMap((consumable) => consumable.subjects);
  const uniqueSubjects = [...new Set(allSsubjects)];
  return uniqueSubjects;
};

export const selectStudentSubjectsAmount: StudentSelector<number> = (state) => selectStudentProfileSubjects(state).length;
