import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { ITeacherView } from "../../../common/types/teacherDomainView";

const externalTeacherInitialState: ITeacherView = {
  id: '',
  name: '',
  description: '',
  avatar: '',
  abonementOffers: [],
  subjects: [],
};

export const externalTeacherSlice = createSlice({
  name: 'externalTeacher',
  initialState: externalTeacherInitialState,
  reducers: {
    setExternalTeacher: (
      state, 
      { payload: externalTeacher }: PayloadAction<ITeacherView>
    ) => externalTeacher,
    resetExternalTeacher: () => ({...externalTeacherInitialState}),
  },
});
