import { HOC } from "../../../common/hoc/types";
import { useStudentSelector } from "../../store";
import { ScheduleView } from "../../store/modal";
import { selectScheduleView } from "../../selectors/novigation";
import { NotificationButton } from "../../../teacher/hoc/NotificationButton";
import { Header } from "../../../common/component/Header";
import { DropDownMenu, IOption } from "../../../common/component/DropDownMenu";

export const StudentScheduleHeader: HOC = () => {
  
  // const dispatch = useDispatch();
  const scheduleMenuOptions: IOption[] = [
      {
          id: ScheduleView.schedule,
          title: 'Schedule',
      },
      {
          id: ScheduleView.week,
          title: 'Week',
      },
  ];
  const currentOption = useStudentSelector(selectScheduleView);
  return (
      <Header>
          <DropDownMenu
              options={scheduleMenuOptions}
              title='Abonement'
              currentOption={currentOption}
              onChange={(id) => {console.log('onChange', id)  }}
          />
          <NotificationButton />
      </Header>
  );
}
