import { all, call, put, select } from "redux-saga/effects";
import { ISubject, ITeacher, id } from "../../common/types/domain";
import { createCommand } from "../../common/utilities/createCommand"
import { AbonementOfferShownTarget, StudentSearchOptions, studentModalSlice } from "../store/modal";
import { domainHandelers, readSubjectViewApiCall, readTeacherViewApiCall } from "../../common/api/domainApi";
import { externalTeacherSlice } from "../store/externalTeacher";
import { ISubjectView, ITeacherView } from "../../common/types/teacherDomainView";
import { selectStudentSearch, selectStudentSearchOption } from "../selectors/novigation";
import { externalSubjectsSlice, externalTeachersSlice } from "../store/externalResources";

export const drilldownTeacher = createCommand<id>(
  'drilldownTeacher',
  function* ({payload: teacherId}) {
    const teacher: ITeacherView = yield call(readTeacherViewApiCall, teacherId);
    const isTeacherValid = !!teacher.id;

    if (isTeacherValid) {
      yield put(externalTeacherSlice.actions.setExternalTeacher(teacher));
    }
  },
);

export const changeSearch = createCommand<string>(
  'changeSearch',
  function* ({payload: searchString}) {
    yield put(studentModalSlice.actions.setModalState({
      search: searchString,
    }))
  },
);

export const searchOptionChange = createCommand<StudentSearchOptions>(
  'searchOptionChange',
  function* ({payload: option}) {
    yield put(studentModalSlice.actions.setModalState({
      searchOption: option,
    }))
  },
);

function* searchTeacherEffect(searchScring: string) {
  const teachersEntities: ITeacher[] = yield call(domainHandelers.teacherAPIHandeler.list, '', { search: searchScring });

  const teacherViewsEffects = teachersEntities.map((teacher) => call(readTeacherViewApiCall, teacher.id));
  const teachers: ITeacherView[] = yield all(teacherViewsEffects);

  yield put(externalTeachersSlice.actions.setResourses(teachers))
}

function* searchSubjectEffect(searchString: string) {
  const subjectsEntities: ISubject[] = yield call(domainHandelers.subjectApiHandeler.list, '', { search: searchString });
  
  const subjectsViewEffects = subjectsEntities.map((subject) => call(readSubjectViewApiCall, subject.id));
  const subjects: ISubjectView[] = yield all(subjectsViewEffects);

  yield put(externalSubjectsSlice.actions.setResourses(subjects));
}

export const startSearch = createCommand<void>(
  'startSearch',
  function* () {
    const search = yield select(selectStudentSearch);
    const searchOption = yield select(selectStudentSearchOption);

    switch (searchOption) {
      case StudentSearchOptions.teacher: {
        yield searchTeacherEffect(search);
        break;
      }
      case StudentSearchOptions.subject: {
        yield searchSubjectEffect(search);
        break;
      }
      case StudentSearchOptions.all: {
        yield searchTeacherEffect(search);
        yield searchSubjectEffect(search);
        break;
      }
    }
  },
);

export const drilldownSubject = createCommand<id>(
  'drilldownSubject',
  function* ({payload: subjectId}) {
    const correspondingSubject: ISubject = yield call(domainHandelers.subjectApiHandeler.single, subjectId);
    const correspondingTeacherId = correspondingSubject.teacherId || '';
    const correspondingTeacher: ITeacherView = yield call(readTeacherViewApiCall, correspondingTeacherId);
    yield put(externalTeacherSlice.actions.setExternalTeacher(correspondingTeacher));
    yield put(studentModalSlice.actions.setModalState({ currentSubjectId: subjectId }))
  },
);

export const openShowcaseForTeacher = createCommand<id>(
  'openShowcaseForTeacher',
  function* ({payload: teacherId}) {
    yield put(studentModalSlice.actions.setModalState({
      abonementOffersShowcaseShown: AbonementOfferShownTarget.teacher
    }));
  },
);

export const openShowcaseForSubject = createCommand<id>(
  'openShowcaseForTeacher',
  function* ({payload: subjectId}) {
    yield put(studentModalSlice.actions.setModalState({
      currentSubjectId: subjectId,
      abonementOffersShowcaseShown: AbonementOfferShownTarget.subject
    }));
  },
);