import { IEditingResourceState, editingFields } from "../../common/store/editingEntitySlice";
import { StudentSelector } from "./types";
import { ArgumentSelector } from "../../common/types/utility";
import { IStudentView } from "../../common/types/studentDomainView";


export const selectEditableStudent: StudentSelector<IEditingResourceState<IStudentView>> = (state) => state.editableStudent;
export const selectStudentData: StudentSelector<IStudentView> = (state) => selectEditableStudent(state).data;
export const selectStudentEditingFields: StudentSelector<editingFields> = (state) => selectEditableStudent(state).fieldsEditing;

export const selectEditableStudentName : StudentSelector<string> = (state) => {
  const studentName = selectStudentData(state).name;
  return studentName;
};

const isStudentFieldEditingSelectorCreator: ArgumentSelector<StudentSelector<boolean>> = (fieldName: string) => {
  return (state) => selectStudentEditingFields(state)[fieldName];
}

export const selectIsNameEditing : StudentSelector<boolean> = isStudentFieldEditingSelectorCreator('name');
  