import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { id } from "../../../common/types/domain";

export enum StudentNavigation {
  schedule = 'schedule',
  studentProfilePage = 'studentProfilePage',
  searchPage = 'searchPage',
}

export enum ScheduleView {
  schedule = 'schedule',
  week = 'week',
}

export enum StudentProfileNavigation {
  subjects = 'subjects',
  abonements = 'abonements',
}

export enum AbonementPageNavigation {
  subjects = 'subjects',
  lessons = 'lessons',
}

export enum DetailsDrawerShown {
  closed = 'closed',
  teacherPageForStudent = 'teacherPageForStudent',
  aboementPageForStudent = 'aboementPageForStudent',
}

export enum AdditionalDrawerShown {
  closed = 'closed',
  subjectPageForStudent = 'subjectPageForStudent',
}

export interface IStudentNavigation {
  studentNavigation: StudentNavigation;
  scheduleView: ScheduleView;
  studentProfileNavigation: StudentProfileNavigation;
  abonementPageNavigation: AbonementPageNavigation;
}

export enum AbonementOfferShownTarget {
  teacher = 'teacher',
  subject = 'subject',
  closed = 'closed',
}

export interface IHaveStudentDrawers {
  detailsDrawerShown: DetailsDrawerShown;
  additionalDrawerShown: AdditionalDrawerShown;
  abonementOffersShowcaseShown: AbonementOfferShownTarget;
}

export interface IHaveShowMore {
  showMore: boolean;
}

export enum StudentSearchOptions {
  all = 'all',
  teacher = 'teacher',
  subject = 'subject',
}

export interface IHaveSearchAndSearchOption {
  search: string;
  searchOption: StudentSearchOptions;
}

export interface IStoreCurrentValues {
  currentDate: Date;
  currentSubjectId?: id;
  currentOwnAbonementId?: id;
  currentAbonementOfferIndex?: number;
}

export interface IStudentModalState extends
  IStudentNavigation,
  IHaveStudentDrawers,
  IHaveShowMore,
  IHaveSearchAndSearchOption,
  IStoreCurrentValues
  {};

const studentModalInitialState: IStudentModalState = {
  studentNavigation: StudentNavigation.schedule,
  scheduleView: ScheduleView.schedule,
  studentProfileNavigation: StudentProfileNavigation.subjects,
  abonementPageNavigation: AbonementPageNavigation.lessons,
  detailsDrawerShown: DetailsDrawerShown.closed,
  showMore: false,
  additionalDrawerShown: AdditionalDrawerShown.closed,
  abonementOffersShowcaseShown: AbonementOfferShownTarget.closed,
  currentDate: new Date(),
  search: '',
  searchOption: StudentSearchOptions.all,
};

export const studentModalSlice = createSlice({
  name: 'teacherModal',
  initialState: studentModalInitialState,
  reducers: {
    setModalState: (state, { payload: stateDiff }: PayloadAction<Partial<IStudentModalState>>) => ({
      ...state,
      ...stateDiff,
    }),
    resetModalState: () => ({...studentModalInitialState}),
  },
});
