import { TypedUseSelectorHook, useSelector } from "react-redux";
import { studentRootReducer } from "./rootReducer";
import { makeStoreCreator } from "../../common/store/utils";
import { studentRootSaga } from "../commands";

export type rootStudentState = ReturnType<typeof studentRootReducer>;

export const useStudentSelector = useSelector as TypedUseSelectorHook<rootStudentState>;

export const studentStoreCreator = makeStoreCreator(studentRootReducer, studentRootSaga);

const studentStore = studentStoreCreator();

export default studentStore;