import { FullCalendarView } from "../../common/component/Calendar";
import {
  StudentProfileNavigation, 
  AbonementPageNavigation, 
  DetailsDrawerShown, 
  StudentNavigation, 
  ScheduleView, 
  StudentSearchOptions,
  AbonementOfferShownTarget
} from "../store/modal";
import { StudentSelector } from "./types";


export const selectStudentNavigation: StudentSelector<StudentNavigation> = (state) => state.modal.studentNavigation;
export const selectStudentDetailsDrawerShown: StudentSelector<DetailsDrawerShown> = (state) => state.modal.detailsDrawerShown;
export const selectStudentAbonementShowcaseShown: StudentSelector<AbonementOfferShownTarget> = (state) => state.modal.abonementOffersShowcaseShown;

export const selectStudentProfileNavigation: StudentSelector<StudentProfileNavigation> = (state) => state.modal.studentProfileNavigation;
export const selectAbonementPageNavigation: StudentSelector<AbonementPageNavigation> = (state) => state.modal.abonementPageNavigation;

export const selectDetaisDrowerShown: StudentSelector<DetailsDrawerShown> = (state) => state.modal.detailsDrawerShown;

export const selectIsShowMore: StudentSelector<boolean> = (state) => state.modal.showMore;

export const selectCurrentDate: StudentSelector<Date> = (state) => state.modal.currentDate;
export const selectScheduleView: StudentSelector<ScheduleView> = (state) => state.modal.scheduleView;

export const photoHeight = 60;
export const avatarHeight = photoHeight - 16;
export const descriptionDefaultHeight = 120;
export const showMoreDescriptionHeight = descriptionDefaultHeight + 180;

export const selectSubjectDescriptionHeight: StudentSelector<number> = (state) => {
  const showMore = selectIsShowMore(state);
  const descriptionHeight = showMore ? showMoreDescriptionHeight : descriptionDefaultHeight;
  return descriptionHeight;
};

export const selectStudentFullCalendarView: StudentSelector<FullCalendarView> = (state) => {

  const scheduleView = selectScheduleView(state);

  switch (scheduleView) {
    case ScheduleView.schedule:
      return FullCalendarView.listWeek;;
    case ScheduleView.week:
      return FullCalendarView.timeGridWeek;
    default:
      return FullCalendarView.timeGridWeek;
  }
};

export const selectStudentSearch: StudentSelector<string> = (state) => state.modal.search;
export const selectStudentSearchOption: StudentSelector<StudentSearchOptions> = (state) => state.modal.searchOption;