
import { HOC } from '../../../common/hoc/types';
import { useStudentSelector } from '../../store';
import { selectStudentNavigation } from '../../selectors/novigation';
import { StudentNavigation } from '../../store/modal';
import { selectStudentAvatar } from '../../selectors/domain';
import { Footer } from '../../../common/component/Footer';
import { IconNavButton, ButtonType } from '../../../common/component/IconNavButton';

export enum StudentFooterControlsClasses {
    navigateSchedule = 'navigateSchedule',
    navigateSearchPage = 'navigateSearchPage',
    navigateStudentProfilePage = 'navigateStudentProfilePage',
};

export const StudentFooter: HOC = () => {
    // const dispatch = useDispatch();
    const studentAvatar = useStudentSelector(selectStudentAvatar);

    const selectedStudentView = useStudentSelector(selectStudentNavigation);
    const scheduleActive = selectedStudentView === StudentNavigation.schedule;
    const studentProfilePageActive = selectedStudentView === StudentNavigation.studentProfilePage;
    const searchPageActive = selectedStudentView === StudentNavigation.searchPage;
    return (
        <Footer>
            <IconNavButton
                className={StudentFooterControlsClasses.navigateSchedule}
                type={ButtonType.schedule}
                onClick={() => console.log('navigateSchedule')}
                isActive={scheduleActive}
            />
            <IconNavButton
                className={StudentFooterControlsClasses.navigateSearchPage}
                type={ButtonType.search}
                onClick={() => console.log('navigateSearchPage')}
                isActive={searchPageActive}
            />
            <IconNavButton
                className={StudentFooterControlsClasses.navigateStudentProfilePage}
                type={ButtonType.avatar}
                onClick={() => console.log('navigateStudentProfilePage')}
                isActive={studentProfilePageActive}
                image={studentAvatar}
            />
        </Footer>
    );
}
