import { Typography } from "@mui/material";
import { HOC } from "../../../common/hoc/types";
import { mochDate2023_7_14_15_30_0 } from "../../../common/component/dummies";
import { useStudentSelector } from "../../store";
import { AbonementPageNavigation } from "../../store/modal";
import AllInclusiveIcon from '@mui/icons-material/AllInclusive';
import { photoHeight, selectAbonementPageNavigation } from "../../selectors/novigation";
import { MetricItemContainer, MetricsContainer } from "../../../common/component/MetricStyled";

export const AbonementInfo: HOC = () => {

  const expiryDate = mochDate2023_7_14_15_30_0;
  const lessonsAttended = 12;
  const lessonsLeft = 8;

  const dateFormatter = new Intl.DateTimeFormat('en-US', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
  });

  const dateRepresentation = dateFormatter.format(expiryDate);

  const abonementPageView = useStudentSelector(selectAbonementPageNavigation);

  const isSubjectsActive = abonementPageView === AbonementPageNavigation.subjects;

  const isLimited = true;

  const showLessonsLeft = isLimited && isSubjectsActive;

  return (
    <MetricsContainer height={photoHeight}>
        <MetricItemContainer>
          <Typography textAlign="center">{dateRepresentation}</Typography>
          <Typography textAlign="center">expiry date</Typography>                
        </MetricItemContainer>
        <MetricItemContainer>
            <Typography textAlign="center">{lessonsAttended}</Typography>
            <Typography textAlign="center">lessons attended</Typography>
        </MetricItemContainer>
        <MetricItemContainer>
          {
            showLessonsLeft ? 
            <>
              <Typography textAlign="center">{lessonsLeft}</Typography>
              <Typography textAlign="center">lessons left</Typography>
            </> : <AllInclusiveIcon  color="success"/>
          }
        </MetricItemContainer>
    </MetricsContainer>
  );
}
