import styled from "@emotion/styled";
import { HOC } from "../../../common/hoc/types";
import { selectStudentProfileNavigation } from "../../selectors/novigation";
import { useStudentSelector } from "../../store";
import { Box } from "@mui/material";
import { StudentProfileNavigation } from "../../store/modal";
import { IconNavButton, ButtonType } from "../../../common/component/IconNavButton";

export const StudentNavigationContainer = styled(Box)`
    display: flex;
    height: 80;
    align-items: center;
    padding: 0.5rem;
    justify-content: space-around;
`;

export const StudentProfileNavigationHoc: HOC = () => {

  // const dispach = useDispatch();

  const studentProfilePageView = useStudentSelector(selectStudentProfileNavigation);
  const isSubjectsActive = studentProfilePageView === StudentProfileNavigation.subjects;
  const isAbonementsActive = studentProfilePageView === StudentProfileNavigation.abonements;

  return (
    <StudentNavigationContainer>
      <IconNavButton
        type={ButtonType.abonements}
        onClick={() => console.log('abonements')}
        title="abonements"
        isActive={isAbonementsActive}
      />
      <IconNavButton
        type={ButtonType.subjects}
        onClick={() => console.log('subjects')}
        title="subjects"
        isActive={isSubjectsActive}
      />
    </StudentNavigationContainer>
  );
}
