import { Box } from "@mui/system";
import { HOC } from "../../../common/hoc/types";
import { AppLayoutFull } from "../../../common/component/AppLayout";
import { SearchField } from "../../../common/component/SearchField";
import { StudentSubjectCard } from "../StudentSubjectList";
import { ISubjectView } from "../../../common/types/teacherDomainView";
import { dummieExternalTeachers, dummieSearchSelectOptions, dummySubjectViews } from "../../../common/component/dummies";
import { ITeacher } from "../../../common/types/domain";
import { Card } from "../../../common/component/Card";
import { IconButton } from "../../../common/component/IconButton";
import { ButtonType } from "../../../common/component/IconNavButton";
import { Avatar, Typography } from "@mui/material";
import { Header } from "../../../common/component/Header";
import { NotificationButton } from "../../../teacher/hoc/NotificationButton";
import styled from "@emotion/styled";
import { useDispatch } from "react-redux";
import { changeSearch, drilldownSubject, drilldownTeacher, openShowcaseForSubject, openShowcaseForTeacher, searchOptionChange, startSearch } from "../../commands/studentSearchPageCommands";
import { StudentSearchOptions } from "../../store/modal";

const showCentreStyles: React.CSSProperties = {
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  height: '100%',
};

const StyledBox = styled(Box)`
  display: flex;
  justify-content: right;
  align-items: center;
  width: 100%;
`;

export const SearchPage: HOC = () => {

  const teachersToView: ITeacher [] = dummieExternalTeachers;
  const subjectsToView: ISubjectView [] = dummySubjectViews;
  const search = 'search';

  const hasTeachers = teachersToView.length> 0;
  const hasSubjects = subjectsToView.length > 0;

  const hasItemToView = hasTeachers || hasSubjects;

  const dispatch = useDispatch();

  return (
    <>
      <Header>
        <StyledBox>
          <NotificationButton />
        </StyledBox>
      </Header>
      <AppLayoutFull onSwipeLeft={console.log} onSwipeRight={console.log}>
        <Box sx = {!hasItemToView ? showCentreStyles : {}}>
        <SearchField 
          text={search} 
          onChangeSearch={(text) => dispatch(changeSearch.action(text))} 
          onSearch={() => dispatch(startSearch.action())}
          searchOptions={dummieSearchSelectOptions}
          currentOption={dummieSearchSelectOptions[0].id}
          onOptionChange={(searchOption) => dispatch(searchOptionChange.action(searchOption as StudentSearchOptions))}
        />
          {hasItemToView &&
            <Box>
              <Box>
                {hasSubjects && subjectsToView.map((subject) => {
                  return (
                    <StudentSubjectCard 
                      {...subject}
                      key={subject.id}
                      onClickCommand={() => dispatch(openShowcaseForSubject.action(subject.id))}
                      infoCommand={() => dispatch(drilldownSubject.action(subject.id))}
                    />
                  );
                })}
              </Box>
              <Box>
                {hasTeachers && teachersToView.map((teacher) => {
                  return (
                    <StudentTeacherCard
                      {...teacher}
                      key={teacher.id}
                      onClickCommand={() => dispatch(openShowcaseForTeacher.action(teacher.id))}
                      infoCommand={() => dispatch(drilldownTeacher.action(teacher.id))}
                    />
                  );
                })}
              </Box>
            </Box>
          }
        </Box>
      </AppLayoutFull>
    </>
  );
};

interface IStudentTeacherCardProps extends ITeacher {
  onClickCommand: () => void;
  infoCommand: () => void;
};

export const StudentTeacherCard: React.FC<IStudentTeacherCardProps> = ({name, avatar, onClickCommand, infoCommand}) => {
  return (
    <Card
      children={[
        <Box display='flex' alignItems='center'>
          <Avatar sizes='large' src={avatar} />
          <Typography padding='0px 10px' variant='h6'> {name}</Typography>        
        </Box>,
        <IconButton
          key='subjectInfo'
          type={ButtonType.info}
          onClick={infoCommand}
        />,
      ]}
      onClick={onClickCommand}
    />
  )
};
