import { Reducer, combineReducers } from "redux";
import { loadingSlice } from "../../common/store/loadingSlice";
import { errorSlice } from "../../common/store/errorSlice";
import { externalLessonsSlice, externalSubjectsSlice, externalTeachersSlice } from "./externalResources";
import { studentEditableSlice } from "./editableResources";
import { studentModalSlice } from "./modal";
import { userProfileSlice } from "../../common/store/userProfile";
import { studentDomainViewSlice } from "./studentView";
import { externalTeacherSlice } from "./externalTeacher";
import { IRootStudentState } from "./rootState";

export const studentRootReducer: Reducer<IRootStudentState> = combineReducers({
  loading: loadingSlice.reducer,
  error: errorSlice.reducer,
  studentDomain: studentDomainViewSlice.reducer,

  externalLessons: externalLessonsSlice.reducer,
  externalSubjects: externalSubjectsSlice.reducer,
  externalTeachers: externalTeachersSlice.reducer,

  externalTeacher: externalTeacherSlice.reducer,

  editableStudent: studentEditableSlice.reducer,
  modal: studentModalSlice.reducer,
  userProfile: userProfileSlice.reducer,
});
