import { Alert, Box } from "@mui/material";
import { dummieExternalLessons } from "../../../common/component/dummies";
import { HOC } from "../../../common/hoc/types";
import { Card } from "../../../common/component/Card";
import { TitleStyledTypography } from "../../../common/component/LessonHeader";
import { ILesson } from "../../../common/types/domain";

interface ILessonCardProps extends ILesson {
  onClickCommand: () => void;
};


const LessonCard: React.FC<ILessonCardProps> = ({date, subject, onClickCommand}) => {
  const dysplayNasme = subject.name;

  const dateFormatter = new Intl.DateTimeFormat('en-US', {
dateStyle: 'medium',
timeStyle: 'short',
  });

  const dateRepresentation = dateFormatter.format(date);

  return (
    <Card 
      children={[
        <TitleStyledTypography>{dysplayNasme}</TitleStyledTypography>,
        <></>,
        <TitleStyledTypography>  {dateRepresentation}</TitleStyledTypography>,
      ]} 
      onClick={onClickCommand}      
    />  
    )
};
  

export const LessonsList: HOC = () => {
  
  const lessons = dummieExternalLessons;

  return (
    <Box height={'100%'} overflow={'auto'}>
      {!!lessons.length ?
        lessons.map((lesson) => {
          return (
            <LessonCard
              key={lesson.id}
              onClickCommand={() => console.log(`click ${lesson.id}`)}
              {...lesson}
            />
          )
        }): 
        <Alert severity="info">this list is empty</Alert>
      }
    </Box>
  );
}
