import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { IStudentView } from "../../../common/types/studentDomainView";

const initialStudentDomain: IStudentView = {
  id: '',
  name: '',
  avatar: '',
  abonementConsumables: [],
};

export const studentDomainViewSlice = createSlice({
  name: 'teacherDomainView',
  initialState: initialStudentDomain,
  reducers: {
    setDomainView: (state, {payload}: PayloadAction<IStudentView>) => ({...payload}),
    resetDomainView: () => ({...initialStudentDomain}),
  },
});
