import { Alert, Box, Typography } from "@mui/material";
import { HOC } from "../../../common/hoc/types";
import { useStudentSelector } from "../../store";
import { Card } from "../../../common/component/Card";
import { IconButton } from "../../../common/component/IconButton";
import { ButtonType } from "../../../common/component/IconNavButton";
import { TitleStyledTypography } from "../../../common/component/LessonHeader";
import { selectStudentProfileAbonements } from "../../selectors/domain";
import { IAbonementConsumableView } from "../../../common/types/studentDomainView";


 interface IStudentAbonementCardProps extends IAbonementConsumableView {
  onClickCommand: () => void;
  infoCommand: () => void;
};

const StudentAbonementCard: React.FC<IStudentAbonementCardProps> = ({ id, price, limitLessons, subjects, onClickCommand, infoCommand  }) => {

  const limitLessonsText = limitLessons ? `${limitLessons} ${limitLessons > 1 ? 'Lessons' : 'Lesson' }` : '';
  const subjectsFormatted = subjects.map((subject) => subject.name).join(', ');

  return (
    <Card
      key={id}
      children={[
        <TitleStyledTypography
        key={`${id}_subjectsFormatted`}
        >
          {subjectsFormatted}
        </TitleStyledTypography>,
        <IconButton
          key='subjectInfo'
          type={ButtonType.info}
          onClick={infoCommand}
        />,
        <Typography
          key={`${id}_price`}
        >
          {price}
        </Typography>,
        <Typography
          key={`${id}_limitLessons`}
        >
          {limitLessonsText}
        </Typography>
      ]}
      onClick={onClickCommand}
    />
  )
}

export const StudentAbonementList: HOC = () => {

  // const dispatch = useDispatch();

  const abonements = useStudentSelector(selectStudentProfileAbonements);

  return (
    <Box height={'100%'} overflow={'auto'}>
      {!!abonements.length ? 
        abonements.map((abonement) => {
          return (
            <StudentAbonementCard 
              {...abonement}
              key={abonement.id}
              onClickCommand={() => console.log(`click ${abonement.id}`)} 
              infoCommand={() =>  console.log(`info ${abonement.id}`)}
            />
          )
        }) : 
        <Alert severity="info">Add your abonement offer</Alert>
      }
    </Box>
  );
}
